.chooseFileInput {
    border: 1px solid rgba(0, 0, 0, 0.23);
    padding: 10px;
    background-color: #fafafa;
    border-radius: 12px;
    position: relative;

    &::before {
        border-bottom: none;
    }

    &::after {
        border-bottom: none;
    }

    &:focused {
        border-bottom: none;
    }

    &:hover {
        &:not(.Mui-disabled) {
            &:before {
                border-bottom: none;
            }
        }
    }
}
